.contact {
  display: flex;
  width: 100%;
  /* height: calc(560px + 20vmin); */
  height: 92vh;
  justify-content: center;
  z-index: 3;
  /* font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; */
  /* align-items: center; */
}
.contact_container {
  width: 80%;
}
.contact_container_container {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  /* align-items: center; */
}
.contact_title {
  text-transform: uppercase;
  font-size: calc(11px + 2.3vmin);
  text-align: center;
  font-weight: 600;
}

form {
  display: flex;
  flex-direction: column;
}

label {
  width: 71%;
  font-size: calc(6px + 1vmin);
  font-weight: 600;

  margin-top: 12px;
  margin-bottom: 12px;
  align-self: center;
}
input {
  border-radius: 4px;
  border-left: 10px;
}

input,
#message {
  width: 70%;
  padding: 10px;
  height: calc(6px + 1vmin);
  border-color: rgb(204, 204, 204);
  border-radius: 4px;
  border: 1px solid #ccc;
  align-self: center;
}

#message {
  height: 200px;
}

input:focus,
#message:focus {
  outline: none;
  transition: all 0.4s ease-in-out 0s;
  box-shadow: rgb(136, 136, 136) 5px 10px 15px;
}

#send {
  width: 40%;
  max-width: 935px;
  font-weight: 600;
  font-size: 16px;
  color: rgb(153, 153, 153);
  background-color: rgb(250, 250, 250);
  text-transform: uppercase;
  margin-top: 50px;
  align-self: center;
  cursor: pointer;
  padding: 10px;
  border-radius: 20px;
  outline: none;
  border-width: 1px;
  border-style: solid;
  border-color: rgb(204, 204, 204);
  border-image: none;
  border-image: initial;
}
#send:hover {
  /* border-bottom: 4px solid rgb(255, 118, 117); */
  transition: all 0.4s ease-in-out 0s;
  color: rgb(250, 249, 249);
  background-color: #ff7676;
}

