  display: flex;
  justify-content: center;
}

.projects_title {
  font-size: 40px;
  font-style: italic;
  margin-top: 0px;
}

.projects_project_container {
  display: grid;
  grid-template-columns: repeat(3, 350px);
  grid-template-rows: repeat(3, 350px);
  grid-template-areas:
    'main_content main_content content_two'
    'main_content main_content content_three'
    'content_four content_five content_six';
  grid-gap: 20px;
  gap: 20px;
  justify-content: center;
}

.project {
  display: flex;
  justify-content: center;
